import { Component, OnInit } from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { IonicPage, NavController, AlertController, ToastController, MenuController } from 'ionic-angular';
import { AuthService } from "../../providers/auth-service";
import { jmyapis } from "../../providers/jmyapis";

@IonicPage({
	name: 'page-auth',
	segment: 'auth',
	priority: 'high'
})

@Component({
  selector: 'page-auth',
  templateUrl: 'auth.html'
})
export class AuthPage implements OnInit {
  public onLoginForm: FormGroup;
  public onRegisterForm: FormGroup;
  auth: string = "login";

  resposeData : any;

  tmpBSK : any;
  userData = {"username":"", 
              "password":"",
              "rpassword":"",
              "email":"",
              "reemail":"",
              "apis":{},
              "name":"",
              "body":{
                "apk_key":"",
                "permiso":"",
                "empresa":""
              }, 
              "error":false,
            };
   
  fromRegistro = {"username":"", 
              "nombre":"", 
              "name":"", 
              "email":"",
              "reemail":"",
              "password":"",
              "rpassword":"",
              "error":false,
              "errorMensaje":"",
              "body":{
                "apk_key":"",
                "permiso":"",
                "empresa":""   
              },
            };
 
  loginRegistrar ={
    "descripcion":"Registrate ",
    "email":"Usuario o Email",
    "recuperar":false,
    "botonLogin":false,
    "nuevoUsuario":true,
  };
  constructor(private _fb: FormBuilder, public nav: NavController, public forgotCtrl: AlertController, public menu: MenuController, public toastCtrl: ToastController, public jmyApis: jmyapis, public authService: AuthService) {
		this.menu.swipeEnable(false);
		this.menu.enable(false);
    var data = JSON.parse(localStorage.getItem('tmpData'));
    if(data!=undefined){
      this.userData.username = data.username;
      this.fromRegistro.errorMensaje ="Usuario no valido";
    }
    console.log(data); //data.username

       this.userData.error=false;
  this.loginRegistrar ={
    "descripcion":" ",
    "email":"Usuario o Email",
    "recuperar":false,
    "botonLogin":true,
    "nuevoUsuario":true,
  };
  }
   randomIntFromInterval(min,max){
    return Math.floor(Math.random()*(max-min+1)+min);
  }
  registro(){
    console.log(this.fromRegistro);
    
    if(  this.fromRegistro.nombre!="" &&
         this.fromRegistro.email!="" &&
         this.fromRegistro.reemail!="" &&
         this.fromRegistro.password!="" &&
         this.fromRegistro.rpassword!=""){


         this.fromRegistro.username = this.fromRegistro.email.split("@",1);
        var rand = this.randomIntFromInterval(10,99);
        this.fromRegistro.username = this.fromRegistro.username+rand; 
      // apk Key de registro inicial
      this.fromRegistro.body.apk_key="938a6b38e5092f1ccaede78f57665fdc"; 
      // permiso inicial 
      this.fromRegistro.body.permiso="1";
      // empresa inicial 
      this.fromRegistro.body.empresa="67";
      this.fromRegistro.name=this.fromRegistro.nombre;

      this.fromRegistro.error=false;

        console.log(this.fromRegistro);
          
      this.authService.postData(this.fromRegistro, "signup").then((result) =>{
      this.resposeData = result;
      console.log(this.resposeData);
        this.tmpBSK = this.resposeData.userData;
          console.log(this.tmpBSK);
          
       if(this.tmpBSK===undefined ){
           console.log("no se pudo registrar por falta de criterios");
          this.fromRegistro.error=true;

          if(!this.resposeData.ex.emain_check)
            this.fromRegistro.errorMensaje ="Correo Electronico no valido";

          if(!this.resposeData.ex.password_check)
            this.fromRegistro.errorMensaje ="Contraseña no valido devé contener mas de 6 caracteres";

          if(!this.resposeData.ex.username_check)
            this.fromRegistro.errorMensaje ="Usuario no valido";

        }else{
        
          localStorage.setItem('tmpData', JSON.stringify({"username": this.fromRegistro.username}) );




            this.userData.username = this.fromRegistro.username;
            this.userData.password = this.fromRegistro.password;

            this.login();
        }

  


    
    }, (err) => {
      this.presentToast("Ocurrio un error de conexión, por favor verifica tu conexión a internet");
    });




    }else{
      this.userData.error=true;
    }
 

  }

  registrate(){
    this.loginRegistrar ={
      "descripcion":"Se parte de nuestra comunidad",
      "email":"Email",
      "recuperar":true,
      "botonLogin":false,
      "nuevoUsuario":false,
    };
  }

  registrateRegresar(){
    this.loginRegistrar ={
      "descripcion":" ",
      "email":"Usuario o Email",
      "recuperar":false,
      "botonLogin":true,
      "nuevoUsuario":true,
    };
    this.userData.error=false;
  }

  ngOnInit() {
    this.onLoginForm = this._fb.group({
      email: ['', Validators.compose([
        Validators.required
      ])],
      password: ['', Validators.compose([
        Validators.required
      ])]
    });

    this.onRegisterForm = this._fb.group({
      fullName: ['', Validators.compose([
        Validators.required
      ])],
      email: ['', Validators.compose([
        Validators.required
      ])],
      password: ['', Validators.compose([
        Validators.required
      ])]
    });
  }

  // go to register page
  // register() {
  //   this.nav.setRoot(RegisterPage);
  // }

  // login and go to home page
  login() {
    console.log(this.userData);
    
    if(this.userData.username && this.userData.password){
      this.userData.apis = this.jmyApis.hostApisList;
      this.authService.postData(this.userData, "login").then((result) =>{
      this.resposeData = result;
      console.log(this.resposeData);
      console.log(this.jmyApis.hostApisList);
      if(this.resposeData.userData){
        localStorage.setItem('userData', JSON.stringify(this.resposeData) ); 
        localStorage.setItem('jmyData', JSON.stringify(this.resposeData.jmyapi) );
        localStorage.setItem('userDataSend', JSON.stringify(this.userData) );
       
            this.nav.setRoot('page-home');
         this.presentToast("Acceso correcto");
      }else{
        this.presentToast("El usuario o contraseña no es valido");
     }}, (err) => {
        this.presentToast("Ocurrio un error de conexión, por favor verifica tu conexión a internet");
      });
     }else{
      this.presentToast("Se requiere de un usuario y contraseña");
     }


    
  }

 presentToast(msg) {
    let toast = this.toastCtrl.create({
      message: msg,
      duration: 6000
    });
    toast.present();
  }

  forgotPass() {
    let forgot = this.forgotCtrl.create({
      title: '¿Olvidó su contraseña?',
      message: "Coloque su correo electrónico para enviarle el link re recuperación de contraseña.",
      inputs: [
        {
          name: 'email',
          placeholder: 'Email',
          type: 'email'
        },
      ],
      buttons: [
        {
          text: 'Cancelar',
          handler: data => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'Recuperar',
          handler: data => {
            console.log('Send clicked');
            let toast = this.toastCtrl.create({
              message: 'Email was sended successfully',
              duration: 3000,
              position: 'top',
              cssClass: 'dark-trans',
              closeButtonText: 'OK',
              showCloseButton: true
            });
            toast.present();
          }
        }
      ]
    });
    forgot.present();
  }

}



